}
.navigation-bar
{
    background-color:#3355ff;
    min-height:40px;
    color:white;
    font-size:20px;
    padding:10px;
}
.navigation-bar *
{
    color:white;
    text-decoration: none;
    margin:10px;
}
.logout
{
    position: absolute;
    top:60px;
    right:5px;
    outline:0;
    border:0;
    margin:10px;
    padding:5px;
    border-radius: 5px;
    background-color: crimson;
    color:white;
    cursor:pointer;
}
.grouplist
{
    padding:20px;
}
.loading
{
    -webkit-animation:load infinite 1s linear;
            animation:load infinite 1s linear;
    color:blue;
    position: absolute;
    top:150px;
    left:30px;
    font-size:18px;
}

.list-group-item
{
    cursor:pointer;
}
